import { useCallback, useReducer, useState } from "react"
import { useMountedRef } from "utils"

interface State<D> {
  error: Error | null
  data: D | null
  stat: 'idle' | 'loading' | 'error' | 'success'
}

const defaultInitialState: State<null> = {
  stat: 'idle',
  data: null,
  error: null
}

const defaultConfig = {
  throwOnError: false
}


const useSafeDispatch = <T>(dispatch: (...args: T[]) => void) => {
  const mountedRef = useMountedRef()
  return useCallback(
    (...args: T[]) => (mountedRef.current ? dispatch(...args) : void 0),
  [dispatch, mountedRef])
}
export const useAsync = <D>(initialState?: Partial<State<D>>, initialConfig?:typeof defaultConfig) => {
  const [state, dispatch] = useReducer((state: State<D>, action: Partial<State<D>>) => ({...state, ...action}), {
    ...defaultInitialState,
    ...initialState
  })
  const [retry, setRetry] = useState(() => () => {})
  const config = {
    ...defaultConfig,
    ...initialConfig
  }
  const safeDispatch = useSafeDispatch(dispatch)
  const setData = useCallback((data: D) => safeDispatch({
    data,
    stat: 'success',
    error:  null
  }), [safeDispatch])
  
  const setError = useCallback((error: Error) => dispatch({
    error,
    stat: 'error',
    data: null
  }), []) 
  const run = useCallback((promise: Promise<D>, runConfig?: {retry: () => Promise<D>}) => {
    if(!promise || !promise.then) {
      throw new Error('请传入promise类型数据')
    }
    setRetry(() => () => {
      if(runConfig?.retry) {
        run(runConfig.retry(), runConfig)
      }
    })
    // dispatch(prevState => ({...prevState, stat: 'loading'}))
    safeDispatch({stat: 'loading'})
    return promise.then((data) => {
      setData(data);
      return data;
    }).catch(err => {
      setError(err) 
      return config.throwOnError ? Promise.reject(err) : err
    })
  }, [config.throwOnError, safeDispatch, setData, setError])
  return {
    isIdle: state.stat === 'idle',
    isLoading: state.stat === 'loading',
    isError: state.stat === 'error',
    isSuccess: state.stat === 'success',
    run,
    retry,
    setData,
    setError,
    ...state
  }
}