import { useEffect, useRef, useState } from "react"

export function isFalsy(value: unknown): boolean{
  return value === 0 ? false : !value
}
export function isVoid(value: unknown):boolean {
  return value === undefined || value === null || value === ''
}

export function cleanObject(object: { [key: string]: unknown }) {
  const result = {...object}
  for(let key in result) {
    const value = result[key]
    if(isVoid(value)) {
      delete result[key]
    }
  }
  return result
}

export function useMount(callback: () => void) {
  useEffect(() => {
    callback()
  }, []) 
}
export function useDebounce<T>(value: T, delay?: number){
  const [ debouncedValue, setDebouncedValue ] = useState(value)
  useEffect(() => {
    const timeout = setTimeout(() => setDebouncedValue(value), delay)
    // useEffect 返回的函数是用于清除上一次的副作用的
    // useEffect 如果传入空数组那么只会只会更新一次
    // 如果不穿第二个参数 怎会没次都更新
    
    return () => clearTimeout(timeout)
  }, [value, delay])
  

  return debouncedValue
}


export function useArray<T>(arr: T[]){
  let [ value, setValue ] = useState(arr)
  function add(item: T) {
    setValue(value.concat(item))
  }
  function removeIndex(index: number) {
    const copy = value;
    copy.splice(index, 1)
    setValue(copy)
  }
  
  function clear() {
    setValue([])
  }
  return {
    value,
    clear,
    removeIndex,
    add
  }
}

export const useDocumentTitle = (title: string, keepOnUnmount: boolean = true) => {
  let oldTitle = useRef(document.title).current
  useEffect(() => {
    document.title = title
    return () => {
      if(!keepOnUnmount) {
        document.title = oldTitle
      }
    }
  }, [title,keepOnUnmount, oldTitle])
}

export const resetRoute = () => window.location.href = window.location.origin

/**
 * @description 用来返回组件的加载状态
*/
export const useMountedRef = () => {
  const mountedRef = useRef(false)
  useEffect(() => {
    mountedRef.current = true
    return () => {
      mountedRef.current = false
    }
  })
  return mountedRef
}

/**
 * 传入一个对象，和键集合，返回对应的对象中的键值对
 * @param obj
 * @param keys
 */
 export const subset = <
 O extends { [key in string]: unknown },
 K extends keyof O
>(
 obj: O,
 keys: K[]
) => {
 const filteredEntries = Object.entries(obj).filter(([key]) =>
   keys.includes(key as K)
 );
 return Object.fromEntries(filteredEntries) as Pick<O, K>;
};