import styled from "@emotion/styled"
import { Button, Drawer, Form, Input, Spin } from "antd"
import { UserSelect } from "components/user-select"
import { useEffect } from "react"
import { useAddProjects, useEditProjects } from "utils/project"
import { useProjectModal, useProjectsQueryKey } from "./util"

export const ProjectModal = () => {
  
  const { close, projectModalOpen, editingProject, isLoading } = useProjectModal()
  // console.log(projectModalOpen);
  const useMutateProject = editingProject ? useEditProjects : useAddProjects
  
  const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectsQueryKey())
  // console.log(mutateAsync);
  
  const [form] = Form.useForm()
  const onFinish = (values: any) => {
    mutateAsync({...editingProject, ...values}).then(() => {
      form.resetFields()
      close()
    })
  }

  const closeModal = () => {
    form.resetFields()
    close()
  }

  const title = editingProject ? '编辑项目' : "创建项目"

  useEffect(() => {
    // console.log(editingProject);
    
    form.setFieldsValue(editingProject)
  }, [editingProject, form])
  return <Drawer forceRender={true} visible={projectModalOpen} onClose={closeModal} width={"100%"}>
    {
      isLoading ? <Spin size="large"></Spin>
      : <>
        <Container>
        <h1>{title}</h1>
        <Form form={form} layout={'vertical'} style={{width: '40rem'}} onFinish={onFinish}>
          <Form.Item label={'名称'} name={'name'} rules={[{required: true, message: '请输入名称'}]}>
            <Input placeholder="请输入项目名称"/>
          </Form.Item>
          <Form.Item label={'部门'} name={'organization'} rules={[{required: true, message: '请输入部门名'}]}>
            <Input placeholder="请输入部门"/>
          </Form.Item>
          <Form.Item label={'负责人'} name={'personId'}>
            <UserSelect defaultOptionName={'负责人'}></UserSelect>
          </Form.Item>
          <Form.Item style={{textAlign: 'right'}}>
            <Button type="primary" htmlType="submit" loading={mutateLoading}>
              提交
            </Button>
          </Form.Item>
        </Form>
        </Container>
        
      </>
    }
    {/* <h1>1123</h1> */}
    <Button onClick={close}>关闭</Button>
  </Drawer>
}

const Container = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
`