import { cleanObject } from "utils";
import { useHttp } from "./http";
import {  QueryKey, useMutation, useQuery } from 'react-query'
import { Kanban } from "types/kanban";
import { useAddConfig, useDeleteConfig, useReorderKanbanConfig } from "./use-optimistic-options";
import { useKanbansQueryKey } from "screens/kanban/util";
export const useKanbans = (param?: Partial<Kanban>) => {
  
  const client = useHttp();
  return useQuery<Kanban[]>(useKanbansQueryKey(), () => client('kanbans', { data: cleanObject(param || {})}))
}



export const useAddKanban = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    (params: Partial<Kanban>) =>
      client(`kanbans`, {
        data: params,
        method: "POST",
      }),
    useAddConfig(queryKey)
  );
};

export const useDeleteKanbans = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation((params: Partial<Kanban>) => client(`kanbans/${params.id}`, {
    method: 'DElETE'
  }), useDeleteConfig(queryKey))
}

export interface SortProps {
  fromId: number
  referenceId: number
  type: "before" | "after"

  fromKanbanId?: number
  toKanbanId?: number
}

export const useReorderKanban = (queryKey: QueryKey) => {
  const client = useHttp()
  return useMutation((params: SortProps) => client(`kanbans/reorder`, {
    method: 'POST',
    data: params
  }), useReorderKanbanConfig(queryKey)) 
}