
import { Button, Form, Input, Modal } from "antd"
import { TaskTypeSelect } from "components/task-type-select"
import { UserSelect } from "components/user-select"
import { useEffect } from "react"
import { useDeleteTasks, useEditTask } from "utils/task"
import { useTaskModal, useTasksQueryKey } from "./util"

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
}
export const TaskModal = () => {
  const [form] = Form.useForm()
  const { editingTask, editingTaskId, close } = useTaskModal()
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(useTasksQueryKey())

  const { mutateAsync } = useDeleteTasks(useTasksQueryKey())
  const onCancel = () => {
    close()
    form.resetFields()
  }

  const onOk = async () => {
    await editTask({...editingTask, ...form.getFieldsValue()})
    close()
  }

  useEffect(() => {
    form.setFieldsValue(editingTask)  
  }, [form, editingTask])

  const startDelete = () => {
    close()
    Modal.confirm({
      title: '确定删除任务吗?',
      okText: '确认',
      cancelText: '取消',
      onOk() { 
        return mutateAsync({id: Number(editingTaskId)})
      }
    })
  }
  return <Modal 
    onOk={onOk} 
    onCancel={onCancel} 
    okText="确认" 
    cancelText="取消"
    confirmLoading={editLoading}
    title="编辑任务"
    visible={!!editingTaskId}
    forceRender={true}
  >
    <Form  {...layout}  initialValues={editingTask} form={form}>
      <Form.Item label="任务名" name={"name"} rules={[{required: true, message: '请填写任务名'}]}>
        <Input />
      </Form.Item>
      <Form.Item label="经办人" name={"processorId"}>
        <UserSelect />
      </Form.Item>
      <Form.Item label="类型" name={"typeId"}>
        <TaskTypeSelect />
      </Form.Item>
      <div style={{textAlign: 'right'}}>
        <Button size="small" onClick={startDelete}>删除</Button>
      </div>
    </Form>
  </Modal>
}