import { Kanban } from "types/kanban";
import { useTasks } from "utils/task";
import { useKanbansQueryKey, useTaskModal, useTasksSearchParams } from "./util";
import taskIcon from 'assets/task.svg'
import bugIcon from 'assets/bug.svg'
import { useTaskTypes } from "utils/task-type";
import styled from "@emotion/styled";
import { Button, Card, Dropdown, Menu, Modal } from "antd";
import { CreateTask } from "./create-task";
import { Task } from "types/task";
import { Mark } from "components/mark";
import { useDeleteKanbans } from "utils/kanban";
import { Row } from "components/lib";
import { Drag, Drop, DropChild } from "components/drag-and-drop";
 
export const TaskTypeIcon = ({id}: {id: number}) => {
  const { data } = useTaskTypes()
  const name = data?.find(taskType => taskType.id === id)?.name
  if(!name) return null

  return <img src={name === 'bug' ? bugIcon : taskIcon} alt="tasktype"/>
}

const TaskCard = ({task}: {task:Task}) => {
  const { startEdit } = useTaskModal()
  const { name: keyword }= useTasksSearchParams()
  return <Card onClick={() => startEdit(task.id)} style={{marginBottom: '.5rem', cursor: 'pointer'}} key={task.id}  >
      <p>
        <Mark name={task.name} keyword={keyword} />
        <TaskTypeIcon id={task.typeId} />
      </p>
    </Card>
}
export const KanbanColumn = ({kanban}: {kanban: Kanban}) => {
  const { data: allTasks } = useTasks(useTasksSearchParams())
  const tasks = allTasks?.filter(task => task.kanbanId === kanban.id)
  return <Container>
    <Row between={true}>
      <h3>{kanban.name}</h3>
      <More kanban={kanban} />
    </Row>
    <TasksContainer>
    <Drop 
      type={'ROW'} 
      direction={'vertical'} 
      droppableId={String(kanban.id)}
    >
      {/* TODO: 这边如果没有高度在内容没有的情况下就拖动不回去了 */}
      <DropChild style={{minHeight: '5px'}}>
      {
        tasks?.map((task, index) =>
          <Drag 
            key={task.id} 
            draggableId={'task' + task.id} 
            index={index}
          >
            <div>
              <TaskCard task={task} key={task.id}/>
            </div>
          </Drag>  
        )
      }    
      </DropChild>
    </Drop>
    
    <CreateTask kanbanId={kanban.id} />
    </TasksContainer>
    
  </Container>
}

const More = ({kanban}: {kanban: Kanban}) => {
  const {mutateAsync} = useDeleteKanbans(useKanbansQueryKey())

  const startDelete = () => {
    Modal.confirm({
      title: '确定删除看板吗?',
      okText: '确认',
      cancelText: '取消',
      onOk() {
        return mutateAsync({id: kanban.id})
      }
    })
  }
  const overlay = () => {
    return <Menu>
      <Menu.Item key={'delete'}>
        <Button type="link" onClick={startDelete}>删除</Button>
      </Menu.Item>
    </Menu>
  }
  return <Dropdown overlay={overlay}>
    <Button type="link">...</Button>
  </Dropdown>
}
export const Container = styled.div`
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  display: flex;
  flex-direction: column;
  padding: 0.7rem .7rem;
  margin-right: 1.5rem;
`

const TasksContainer = styled.div`
  overflow: scroll;
  flex: 1;
  ::-webkit-scrollbar {
    display: none;
  }
`