import React from 'react';
import { Dropdown, Menu, Table, TableProps } from 'antd'
import dayjs from 'dayjs'
import { Link } from 'react-router-dom'
import { Project } from 'types/project';
import { User } from 'types/user';
import { Pin } from 'components/pin';
import { useDeleteProjects, useEditProjects } from 'utils/project';
import { ButtonNoPadding } from 'components/lib';
import { useProjectModal, useProjectsQueryKey } from './util';
import confirm from 'antd/lib/modal/confirm';
import { useDeleteConfig } from 'utils/use-optimistic-options';
// export interface Project {
//   id: string
//   name: string
//   personId: string
//   organization: string
//   created: number
// }
interface ListProps extends TableProps<Project> {
  users: User[],
  refresh?: () => void
}
export const List = ({users, ...props}: ListProps) => {

  
  const { mutate }  = useEditProjects(useProjectsQueryKey())
  
  
  const pinProject = (id: number) => (pin: boolean) => mutate({id, pin})

  
  return <Table 
    pagination={false} 
    rowKey={"id"}
    columns={[{
      title: <Pin checked={true} disabled={true} />,
      render(value, project) {
        return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)}></Pin>
      }
    },{
    title: '名称',
    dataIndex: 'name',
    key: 'name',
    sorter: (a, b) => a.name.localeCompare(b.name),
    render(text, project) {
      return <Link to={`projects/${project.id}`}>{project.name}</Link>
    }
  }, {
    title: '部门',
    key: 'organization',
    dataIndex: 'organization'
   }, {
    title: '负责人',
    key: 'personId',
    render(text, project) {
      return <span>{users.find(user => user.id === project.personId)?.name || '未知'}</span>
    },
  },
  {
    title: '创建时间',
    key: 'created',
    dataIndex: 'created',
    render(text, project) {
      return <span>
        {
          project.created ? dayjs(project.created).format('YYYY-MM-DD') : ''
        }
      </span>
    }
   },
   {
     render(value, project) {
       return <More project={project} />
     } 
   }
  ]} {...props}>
  </Table>
}

const More = ({project}: {project: Project}) => {

  const { startEdit } = useProjectModal()
  
  const editingProject = (id: number) => () => startEdit(id) 
  const { mutate: deleteProject } = useDeleteProjects(useProjectsQueryKey())
  const confrimDeleteProject = (id: number) => {
    return confirm({
      title: '确定删除这个项目吗?',
      okText: '确定删除',
      cancelText: '取消',
      onOk() {
        deleteProject({id})
      }
    })
  }
  return <Dropdown
  overlay={
    <Menu>
      <Menu.Item key="edit" onClick={editingProject(project.id)}>编辑</Menu.Item>
      <Menu.Item key="delete" onClick={() => confrimDeleteProject(project.id)}>删除</Menu.Item>
    </Menu>  
  }
 >
   <ButtonNoPadding type="link">...</ButtonNoPadding>
 </Dropdown>
}