/** @jsxImportSource @emotion/react */
import { Form, Input, Select } from 'antd';
import { UserSelect } from 'components/user-select';
import React from 'react';
import { User } from 'types/user';
import { Project } from 'types/project'
import { useProjectsSearchParams } from './util';
interface SearchPanelProps {
  users: User[]
  // param: Partial<Pick<Project, 'name' | 'personId'>> 
  // setParam: (param: SearchPanelProps['param']) => void
}
export const SearchPanel = ({ users }: SearchPanelProps) => {
  const [ param, setParam ] = useProjectsSearchParams()
  return <Form css={{marginBottom: '2rem'}}  layout={'inline'}>
    <Form.Item>
      <Input placeholder="项目名" type="text" value={param.name} onChange={evt => setParam({
        ...param,
        name: evt.target.value
      })} />
    </Form.Item>
    <Form.Item>
      <UserSelect value={param.personId} onChange={value => setParam({
        ...param,
        personId: value
      })} defaultOptionName="负责人"></UserSelect>
    </Form.Item>

  </Form>
}