import { Select } from "antd";
import { Raw } from "types";

export interface IdSelectProps extends Omit<React.ComponentProps<typeof Select>, 'value' | 'onChange' | 'options'>{
  value?: Raw | undefined | null,
  onChange?: (evt?: number) => void,
  defaultOptionName?: string,
  options?: {name: string, id: number}[] 
}

/**
 * 
 * onChange 只会会调 number | undefined
 * @returns 
 */
export const IdSelect = (props: IdSelectProps) => {
  const { value, onChange, defaultOptionName, options, ...restProps } =  props
  return <Select 
    value={options?.length ? toNumber(value) : 0} 
    onChange={(value) => onChange?.(toNumber(value) || undefined)}
    {...restProps}
  >
    {
      defaultOptionName ? <Select.Option value={0}>{defaultOptionName}</Select.Option> : null
    }
    {
      options?.map(option => 
        <Select.Option key={option.id} value={option.id}>{option.name}</Select.Option>
      )
    }
  </Select> 
}

const toNumber = function(value: unknown){
  return isNaN(Number(value)) ? 0 : Number(value)
}