// 项目列表搜索的参数

import { useMemo } from "react"
import { useProject } from "utils/project"
import { useUrlQueryParam, useSetUrlSearchParam } from "utils/url"

export const useProjectsSearchParams = () => {
  const [param, setParam] = useUrlQueryParam(['name', 'personId'])
  const projectsParam = useMemo(() => ({...param, personId: Number(param.personId) || undefined}),  [param]) 
  return [
    projectsParam,
    setParam
  ] as const
}

export const useProjectModal = () => {
  const [{projectCreate}, setProjectCreate] = useUrlQueryParam(['projectCreate'])

  const [{editingProjectId}, setEditdingProjectId] = useUrlQueryParam(['editingProjectId'])

  const { isLoading, data: editingProject} = useProject(Number(editingProjectId))
  
  const startEdit = (id: number) => setEditdingProjectId({editingProjectId: id})

  const setUrlParams = useSetUrlSearchParam();

  const open = () => setProjectCreate({projectCreate: true})
  
  const close = () => setUrlParams({ projectCreate: "", editingProjectId: "" });
  
  return {
    projectModalOpen: projectCreate === 'true' || Boolean(editingProjectId),
    open,
    close,
    editingProject,
    startEdit,
    isLoading
  }
}

export const useProjectsQueryKey = () => {
  const [params] = useProjectsSearchParams();
  return ["projects", params];
};